import Vue from 'vue'
import Router from 'vue-router'
// 处理高版本router的不兼容报错问题
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
Vue.use(Router)

export default new Router({
  routes: [
		{ 	
			path: '/',
			redirect: 'login'
		},
		{
			path:'/report',
			meta: { title: '报表统计'},
			component:resolve =>require( ['../components/home'], resolve),
			children:[
				{
					path: '/',//欢饮页
					meta: { title: 'menu.report' },
					component: resolve => require(['../views/Report/Report'], resolve)    
				},
				{
					path: '/exportlog',
					meta: { title: 'menu.logsexport' },
					component: resolve => require(['../views/Logs/Logsexport/Logsexport'], resolve)    
				},
				{
					path: '/user',
					meta: { title: 'menu.user' },
					component: resolve => require(['../views/User/User'], resolve)    
				},
				{
					path: '/upload',
					meta: { title: 'menu.upload' },
					component: resolve => require(['../views/files/upload/upload'], resolve)    
				}
			]
		},
		{
			path: '/login',
			name: 'Login',
			component: resolve => require(['../views/Login/Login'], resolve),
		}
	]
})