// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueI18n from 'vue-i18n'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import enLocale from 'element-ui/lib/locale/lang/en'        //引入Element UI的英文包
import zhLocale from 'element-ui/lib/locale/lang/zh-CN'     //引入Element UI的中文包
import axios from 'axios';//引入axios
import echarts from 'echarts'
import  'echarts/theme/macarons.js'
import App from './App'
import router from './router'
// vuex引入
import store from './store/index.js'
import 'lib-flexible/flexible.js'

//测试vconsole
// import Vconsole from 'vconsole'
// let vConsole = new Vconsole();

// export default vConsole

Vue.prototype.$http = axios;//将axios挂载到Vue实例中的$ajax上面,在项目中的任何位置通过this.$http使用
Vue.use(VueI18n);


Vue.prototype.$echarts = echarts //通过原型引用

const i18n = new VueI18n({
    locale: localStorage.getItem('lang')||'zh',    // 语言标识,通过本地存储，避免刷新页面丢失
    //S// 通过切换locale的值来实现语言切换
    messages:{
      'en':Object.assign(require('./lang/en.js'),enLocale),
      'zh':Object.assign(require('./lang/zh.js'),zhLocale),
    }
})
Vue.use(ElementUI, {
  i18n: (key, value) => i18n.t(key, value)
});
Vue.config.productionTip = false

//路由守卫
router.beforeEach((to, from, next) => {

    if (localStorage.getItem('user_group_power')&&localStorage.getItem('user_name')) {
      next()
    } else {
      if (to.path == "/login") {
        next()
      } else {
        Vue.prototype.$message({
          showClose: true,
          message: '登录信息失效，请重新登录',
          type: 'error'
        })
        setTimeout(function() {
          next({
            path: '/login'
          });
        }, 2000)

      }

    }
})
//按钮级别的权限控制**权限指令**///

Vue.directive('has', {
  bind: function (el, binding) {
    if (!Vue.prototype.$_has(binding.value)) {
      // el.parentNode.removeChild(el);
      el.style.display = 'none';
    }
  }
});
//权限检查方法
Vue.prototype.$_has = function (value) {
  let isExist = false;
  //  获取拥有的权限
  let buttonperms = sessionStorage.getItem("btnPermissions")
  // 权限列表
  //console.log(value); // aa bb
  if (buttonperms <= value) {
    isExist = true;
  }
  return isExist;
}


new Vue({
	router,
	store,
	i18n,
	render: h => h(App),
}).$mount('#app')
